<script>
export default {
	data() {
		return {
			ruleForm: {
			},
			forgetForm: {},
            pageFlag : '',
			tableName:"",
			rules: {},
            yezhuxingbieOptions: [],
		};
	},
	mounted(){
		this.pageFlag = this.$route.query.pageFlag
		if(this.$route.query.pageFlag=='register'){
			
			let table = this.$storage.get("loginTable");
			this.tableName = table;
			if(this.tableName=='yezhu'){
				this.ruleForm = {
					yezhuzhanghao: '',
					mima: '',
					yezhuxingming: '',
					xingbie: '',
					lianxifangshi: '',
					touxiang: '',
					pquestion: '',
					panswer: '',
				}
			}
			if(this.tableName=='gongsi'){
				this.ruleForm = {
					gongsimingcheng: '',
					mima: '',
					fuzeren: '',
					gongsifengmian: '',
					lianxifangshi: '',
					gongsiyouxiang: '',
					gongsidizhi: '',
					chuanglishijian: '',
					shejituandui: '',
					gongsijianjie: '',
					sfsh: '',
					shhf: '',
					clicktime: '',
					clicknum: '',
					storeupnum: '',
				}
			}
			if ('yezhu' == this.tableName) {
				this.rules.yezhuzhanghao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yezhu' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yezhu' == this.tableName) {
				this.rules.yezhuxingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('gongsi' == this.tableName) {
				this.rules.gongsimingcheng = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('gongsi' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			this.yezhuxingbieOptions = ",".split(',')
			this.ruleForm.chuanglishijian = this.getCurDate()
		}
	},
	created() {
	},
	destroyed() {
		  	},
	methods: {
		changeRules(name){
			if(this.rules[name]){
				return true
			}
			return false
		},
		// uuid
		getUUID () {
			return new Date().getTime();
		},
		close(){
			this.$router.push({ path: "/login" });
		},
        yezhutouxiangUploadChange(fileUrls) {
            this.ruleForm.touxiang = fileUrls;
        },
        gongsigongsifengmianUploadChange(fileUrls) {
            this.ruleForm.gongsifengmian = fileUrls;
        },

        // 


		// 
		login() {
			var url=this.tableName+"/register";
					if((!this.ruleForm.yezhuzhanghao) && `yezhu` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.mima) && `yezhu` == this.tableName){
						this.$message.error(``);
						return
					}
					if((this.ruleForm.mima!=this.ruleForm.mima2) && `yezhu` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.yezhuxingming) && `yezhu` == this.tableName){
						this.$message.error(``);
						return
					}
					if(`yezhu` == this.tableName && this.ruleForm.lianxifangshi &&(!this.$validate.isMobile(this.ruleForm.lianxifangshi))){
						this.$message.error(``);
						return
					}
            if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
            }
			  if(this.tableName=='gongsi'){
				  this.ruleForm.sfsh = ''
			  }
					if((!this.ruleForm.gongsimingcheng) && `gongsi` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.mima) && `gongsi` == this.tableName){
						this.$message.error(``);
						return
					}
					if((this.ruleForm.mima!=this.ruleForm.mima2) && `gongsi` == this.tableName){
						this.$message.error(``);
						return
					}
            if(this.ruleForm.gongsifengmian!=null) {
                this.ruleForm.gongsifengmian = this.ruleForm.gongsifengmian.replace(new RegExp(this.$base.url,"g"),"");
            }
					if(`gongsi` == this.tableName && this.ruleForm.gongsiyouxiang &&(!this.$validate.isEmail(this.ruleForm.gongsiyouxiang))){
						this.$message.error(``);
						return
					}
			this.$http({
				url: url,
				method: "post",
				data:this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.$router.replace({ path: "/login" });
						}
					});
				} else {
					this.$message.error(data.msg);
				}
			});
		}
	}
};
