</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("URL"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				gongsimingcheng : false,
				fuzeren : false,
				gongsifengmian : false,
				lianxifangshi : false,
				yuyueshijian : false,
				yuyueneirong : false,
				yezhuzhanghao : false,
				yezhuxingming : false,
				fangwumingcheng : false,
				louceng : false,
				fangjianhao : false,
				mianji : false,
				fangxing : false,
				weizhi : false,
				sfsh : false,
				shhf : false,
			},
			
			
			ruleForm: {
				gongsimingcheng: '',
				fuzeren: '',
				gongsifengmian: '',
				lianxifangshi: '',
				yuyueshijian: '',
				yuyueneirong: '',
				yezhuzhanghao: '',
				yezhuxingming: '',
				fangwumingcheng: '',
				louceng: '',
				fangjianhao: '',
				mianji: '',
				fangxing: '',
				weizhi: '',
				shhf: '',
			},
		
			fangwumingchengOptions: [],

			
			rules: {
				gongsimingcheng: [
				],
				fuzeren: [
				],
				gongsifengmian: [
				],
				lianxifangshi: [
				],
				yuyueshijian: [
				],
				yuyueneirong: [
				],
				yezhuzhanghao: [
				],
				yezhuxingming: [
				],
				fangwumingcheng: [
				],
				louceng: [
				],
				fangjianhao: [
				],
				mianji: [
				],
				fangxing: [
				],
				weizhi: [
				],
				sfsh: [
				],
				shhf: [
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
		this.ruleForm.yuyueshijian = this.getCurDateTime()
	},
	methods: {
		
		// 
		download(file){
			window.open(`${file}`)
		},
		// 
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='gongsimingcheng'){
							this.ruleForm.gongsimingcheng = obj[o];
							this.ro.gongsimingcheng = true;
							continue;
						}
						if(o=='fuzeren'){
							this.ruleForm.fuzeren = obj[o];
							this.ro.fuzeren = true;
							continue;
						}
						if(o=='gongsifengmian'){
							this.ruleForm.gongsifengmian = obj[o];
							this.ro.gongsifengmian = true;
							continue;
						}
						if(o=='lianxifangshi'){
							this.ruleForm.lianxifangshi = obj[o];
							this.ro.lianxifangshi = true;
							continue;
						}
						if(o=='yuyueshijian'){
							this.ruleForm.yuyueshijian = obj[o];
							this.ro.yuyueshijian = true;
							continue;
						}
						if(o=='yuyueneirong'){
							this.ruleForm.yuyueneirong = obj[o];
							this.ro.yuyueneirong = true;
							continue;
						}
						if(o=='yezhuzhanghao'){
							this.ruleForm.yezhuzhanghao = obj[o];
							this.ro.yezhuzhanghao = true;
							continue;
						}
						if(o=='yezhuxingming'){
							this.ruleForm.yezhuxingming = obj[o];
							this.ro.yezhuxingming = true;
							continue;
						}
						if(o=='fangwumingcheng'){
							this.ruleForm.fangwumingcheng = obj[o];
							this.ro.fangwumingcheng = true;
							continue;
						}
						if(o=='louceng'){
							this.ruleForm.louceng = obj[o];
							this.ro.louceng = true;
							continue;
						}
						if(o=='fangjianhao'){
							this.ruleForm.fangjianhao = obj[o];
							this.ro.fangjianhao = true;
							continue;
						}
						if(o=='mianji'){
							this.ruleForm.mianji = obj[o];
							this.ro.mianji = true;
							continue;
						}
						if(o=='fangxing'){
							this.ruleForm.fangxing = obj[o];
							this.ro.fangxing = true;
							continue;
						}
						if(o=='weizhi'){
							this.ruleForm.weizhi = obj[o];
							this.ro.weizhi = true;
							continue;
						}
				}
				
















			}
			
			// 
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					
					var json = data.data;
					if(((json.yezhuzhanghao!=''&&json.yezhuzhanghao) || json.yezhuzhanghao==0) && this.$storage.get("role")!=""){
						this.ruleForm.yezhuzhanghao = json.yezhuzhanghao
						this.ro.yezhuzhanghao = true;
					}
					if(((json.yezhuxingming!=''&&json.yezhuxingming) || json.yezhuxingming==0) && this.$storage.get("role")!=""){
						this.ruleForm.yezhuxingming = json.yezhuxingming
						this.ro.yezhuxingming = true;
					}
				} else {
					this.$message.error(data.msg);
				}
			});
			
            this.$http({
				url: `option/fangwuxinxi/fangwumingcheng`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.fangwumingchengOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
			
		},
			// 
			fangwumingchengChange () {
				this.$http({
					url: `follow/fangwuxinxi/fangwumingcheng?columnValue=`+ this.ruleForm.fangwumingcheng,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						if(data.data.louceng){
							this.ruleForm.louceng = data.data.louceng
						}
						if(data.data.fangjianhao){
							this.ruleForm.fangjianhao = data.data.fangjianhao
						}
						if(data.data.mianji){
							this.ruleForm.mianji = data.data.mianji
						}
						if(data.data.fangxing){
							this.ruleForm.fangxing = data.data.fangxing
						}
						if(data.data.weizhi){
							this.ruleForm.weizhi = data.data.weizhi
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			},
    // 

    info(id) {
      this.$http({
        url: `yuyuexinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //
        let reg=new RegExp('../../../upload','g')//g
        this.ruleForm.yuyueneirong = this.ruleForm.yuyueneirong.replace(reg,'../../../springbootlqyk36m6/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 
    onSubmit() {



	if(this.ruleForm.gongsifengmian!=null) {
		this.ruleForm.gongsifengmian = this.ruleForm.gongsifengmian.replace(new RegExp(this.$base.url,"g"),"");
	}














var objcross = this.$storage.getObj('crossObj');
      //
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "yuyuexinxi/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `yuyuexinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.yuyuexinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `yuyuexinxi/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.yuyuexinxiCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.yuyuexinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    gongsifengmianUploadChange(fileUrls) {
	    this.ruleForm.gongsifengmian = fileUrls;
    },
  }
};
</script>
